import Base from '../Base';
import {
  render,
  fireEvent,
  screen,
  waitFor,
} from '@testing-library/react-native';
import React from 'react';
import fetchMock from 'jest-fetch-mock';
import Geolocation from 'react-native-geolocation-service';
import {useDispatch} from 'react-redux';
import {NativeStackNavigationProp} from '@react-navigation/native-stack';
import {useNavigation} from '@react-navigation/native';
import '@testing-library/jest-dom';
import {Button} from 'react-native';
declare var global: any;
jest.mock('react-redux', () => ({
  useDispatch: jest.fn(),
}));
const timestamp = new Date(Date.now()).toLocaleString([], {
  year: 'numeric',
  month: 'numeric',
  day: 'numeric',
  hour: '2-digit',
  minute: '2-digit',
});
const navigation = useNavigation<NativeStackNavigationProp<any>>();
jest.mock('react-native-geolocation-service', () => {
  return {
    getCurrentPosition: jest.fn(success => {
      const location = {
        coords: {
          latitude: 17.3920466,
          longitude: 78.4758037,
        },
      };
      success(location);
    }),
  };
});
jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: jest.fn().mockReturnValue({
      navigate: jest.fn(),
    }),
  };
});

describe('Locations List Screen/Base Component', () => {
  let latitude: number;
  let longitude: number;
  const mockDispatch = jest.fn();
  let useDispatchMock: jest.Mock;
  const fetchMock = jest.fn();
  global.fetch = fetchMock;
  beforeAll(async () => {
    await new Promise<void>(resolve => {
      Geolocation.getCurrentPosition(position => {
        latitude = position.coords.latitude;
        longitude = position.coords.longitude;
        resolve();
      });
    });
  });

  beforeEach(() => {
    useDispatchMock = useDispatch as jest.Mock;
    useDispatchMock.mockReturnValue(mockDispatch);
  });

  it('renders the base component correctly', () => {
    render(<Base />);
    const currentLabel = screen.getByTestId('list-current-label');
    expect(currentLabel.props.children).toBe('Current Location');
  });
  it('should have the right location', () => {
    render(<Base />);
    expect(Geolocation.getCurrentPosition).toHaveBeenCalled();
  });
  it('renders the current location time', async () => {
    const currentLocation = {
      address: 'unnamed road',
      time: timestamp,
      latitude: 42.123456,
      longitude: -71.123456,
    };
    const {getByTestId} = render(<Base />);
    const currentLocationTime = await waitFor(() =>
      getByTestId('list-current-time'),
    );
    expect(currentLocationTime.props.children).toBe(currentLocation.time);
  });
  it('renders the current location address', async () => {
    const currentLocation = {
      address:
        'unnamed road, Ward 78 Gunfoundry, Hyderabad - 500001, Telangana, India',
      time: timestamp,
      latitude,
      longitude,
    };
    const {getByTestId} = render(<Base />);
    const mockJsonPromise = Promise.resolve({
      results: [{formatted: currentLocation.address}],
    });
    const mockFetchPromise = Promise.resolve({
      json: () => mockJsonPromise,
    });
    fetchMock.mockReturnValueOnce(mockFetchPromise as Promise<Response>);
    const currentLocationAddress = await waitFor(() =>
      getByTestId('list-current-name'),
    );
    console.log(currentLocationAddress);
    expect(currentLocationAddress.props.children).toBe(currentLocation.address);
  });
  // it('renders the whole location item correctly', async () => {
  //   const currentLocation = {
  //     address:
  //       'unnamed road, Ward 78 Gunfoundry, Hyderabad - 500001, Telangana, India',
  //     time: timestamp,
  //     latitude,
  //     longitude,
  //   };
  //   const {getByTestId} = render(<Base />);
  //   const currentLocationItem = await waitFor(() =>
  //     getByTestId('list-current-item'),
  //   );
  //   fireEvent.press(currentLocationItem);
  //   expect(navigation.navigate).toHaveBeenCalledWith('Map', {
  //     address: currentLocation,
  //   });
  // });
  it('clears all locations on button press', async () => {
    const clearAllLocations = jest.fn();
    const {getByTestId} = render(
      <Button
        title="Clear All Locations"
        onPress={clearAllLocations}
        testID="list-clear-all-button"
      />,
    );
    const clearAllButton = await waitFor(() =>
      getByTestId('list-clear-all-button'),
    );
    fireEvent.press(clearAllButton);
    expect(clearAllLocations).toHaveBeenCalled();
  });
  // it('should display previous location name after 5 minutes', async () => {
  //   const {queryByTestId, getByTestId} = render(<Base />);
  //   const previousLocationName = getByTestId('List-previous-name-0');
  //   expect(previousLocationName).toBeNull();
  //   await waitFor(() => {
  //     expect(queryByTestId('List-previous-name-0')).not.toBeNull();
  //   });

  //   const firstPreviousLocationName = getByTestId('List-previous-name-0');
  //   expect(firstPreviousLocationName.props.children).toContain(
  //     'some location name',
  //   );
  // });
});
